/***********  tooltip ********/

.tooltip {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 39px;
  height: 63px;
  background-color: #e60001;
  color: #fff;
  font-size: 9px;
  margin-left: 50px;
  outline-style: none;
} 

/****************  ******************/


.wrapper {

  background-color: "#E6000";
  border-radius: 5px;
  margin-left: 50px;
  background: #FBFBFB;
  border-radius: 5px;


}
