.dashboard {
  &__content {
    font-size: 18px;
  }

  &__charts {
    display: flex;
    justify-content: space-between;
    height: 80vh;
  }

  &__chartsLinear {
    width: 72%;
    height: fit-content;

  }

  &__flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__nutrients {
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.dashboard {
  &__threeGraph {
    display: flex;
    flex-direction: row;
    margin: 50px;
    flex-wrap: nowrap;
    width: 70%;
    gap: 30px;
    position: relative;
    bottom: 30%;
  }



}