
.score {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #fbfbfb;
	width: 30%;
	min-width: 258px;
	height: 263px;
	&__objectif {
		position: absolute;
		border-radius: 50%;
		width: 140px;
		height: 140px;
		background-color: white;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	h3 {
		font-size: 28px;
		font-weight: 700;
		margin: 25px 0 0;
		color: #282d30;
	}
	h2 {
		position: absolute;
		font-size: 16px;
		font-weight: 500;
		top: 10px;
		left: 20px;
		width: 50%;
		line-height: 24px;
	}
}
